import {CUSTOMER_LOADING, CUSTOMER_ERROR, GET_CUSTOMERS, GET_SELECTED_CUSTOMER, UPDATE_CUSTOMER} from '../actions/constants';
const initialState = {
};

export default function(state = initialState, action) {
    switch(action.type) {
        case CUSTOMER_LOADING:
            return {
                ...state,
                loading: action.payload.loading
            }
        case GET_CUSTOMERS:
            return {
                ...state,
                customers: action.payload.customers,
                loading: action.payload.loading
            };
        case CUSTOMER_ERROR:
            return {
                ...state,
                error: action.payload.error,
                loading: action.payload.loading
            };
        case GET_SELECTED_CUSTOMER:
            return {
                ...state,
                selectedCustomer:action.payload.selectedCustomer,
                loading:action.payload.loading
            }
        default:
            return state;
    }
}
