import axios from 'axios';
import {BASE_URL, GET_CUSTOMERS, GET_SELECTED_CUSTOMER, CUSTOMER_ERROR, CUSTOMER_LOADING, UPDATE_CUSTOMER} from './constants'

export const getCustomers = () => dispatch => {
    dispatch({
        type: CUSTOMER_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "customers/customers")
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_CUSTOMERS,
                    payload: {
                        customers:response.data.customers,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: CUSTOMER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const getCustomerById = (id) => dispatch => {
    dispatch({
        type: CUSTOMER_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "customers/customer/" + id)
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_SELECTED_CUSTOMER,
                    payload: {
                        selectedCustomer:response.data.customer,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: CUSTOMER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const updateCustomerById = (id, body) => dispatch => {
    dispatch({
        type: CUSTOMER_LOADING,
        payload: {
            loading:true
        }
    });
    axios.put(BASE_URL + "customers/customer/" + id, body)
        .then(response => {
            if(response.data.status==="success"){
                axios.get(BASE_URL + "customers/customers")
                    .then(res => {
                        if(res.data.status==="success"){
                            dispatch({
                                type: GET_CUSTOMERS,
                                payload: {
                                    customers:res.data.customers,
                                    loading: false
                                }
                            });
                        }

                    })
                    .catch(error => {
                        dispatch({
                            type: CUSTOMER_ERROR,
                            payload: {
                                loading:false
                            }
                        });
                    })
            }

        })
        .catch(error => {
            dispatch({
                type: CUSTOMER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const addNewCustomer = (body) => dispatch => {
    dispatch({
        type: CUSTOMER_LOADING,
        payload: {
            loading:true
        }
    });
    axios.post(BASE_URL + "customers/customers", body)
        .then(response => {
            if(response.data.status==="success"){
                axios.get(BASE_URL + "customers/customers")
                    .then(res => {
                        if(res.data.status==="success"){
                            alert("Seller added successfully.")
                            dispatch({
                                type: GET_CUSTOMERS,
                                payload: {
                                    customers:res.data.customers,
                                    loading: false
                                }
                            });
                        }

                    })
                    .catch(error => {
                        dispatch({
                            type: CUSTOMER_ERROR,
                            payload: {
                                loading:false
                            }
                        });
                    })
            }

        })
        .catch(error => {
            dispatch({
                type: CUSTOMER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

