import axios from 'axios';
import {
    BASE_URL,
    GET_INQUIRIES,
    GET_INQUIRY_BY_ID,
    INQUIRIES_ERROR,
    INQUIRIES_LOADING
} from './constants'

export const getInquiries = () => dispatch => {
    dispatch({
        type: INQUIRIES_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "inquiries/inquiries")
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_INQUIRIES,
                    payload: {
                        inquiries:response.data.inquiries,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: INQUIRIES_ERROR,
                payload: {
                    loading:false,
                    error:error
                }
            });
        })
}

export const getInquiryById = (id) => dispatch => {
    dispatch({
        type: INQUIRIES_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "inquiries/inquiry/" + id)
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_INQUIRY_BY_ID,
                    payload: {
                        selectedInquiry:response.data.inquiry,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: INQUIRIES_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const updateInquiryById = (id, body) => dispatch => {
    dispatch({
        type: INQUIRIES_LOADING,
        payload: {
            loading:true
        }
    });
    axios.put(BASE_URL + "inquiries/inquiry/" + id, body)
        .then(response => {
            if(response.data.status==="success"){
                axios.get(BASE_URL + "inquiries/inquiries")
                    .then(res => {
                        if(res.data.status==="success"){
                            dispatch({
                                type: GET_INQUIRIES,
                                payload: {
                                    inquiries:res.data.inquiries,
                                    loading: false
                                }
                            });
                        }

                    })
                    .catch(error => {
                        dispatch({
                            type: INQUIRIES_ERROR,
                            payload: {
                                loading:false
                            }
                        });
                    })
            }

        })
        .catch(error => {
            dispatch({
                type: INQUIRIES_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

