export const updateSelectedInquiry = (selectedInquiry, key, value) => dispatch => {
    selectedInquiry[key] = value;
    dispatch({
        type: GET_INQUIRY_BY_ID,
        payload: {
            selectedInquiry:selectedInquiry,
            loading: false
        }
    });
}
