import {SIGNUP_LOADING, LOGIN_ERROR, SIGNUP_ERROR, GET_ERRORS, REMOVE_USER, SET_CURRENT_USER} from '../actions/constants';
import isEmpty from '../helpers/is-empty'
const initialState = {
    user: {}
};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_ERRORS:
            return action.payload;
        case SET_CURRENT_USER:
            return {
                ...state,
                user: action.payload.user,
                loading: action.payload.loading,
                isAuthenticated: action.payload.isAuthenticated
            };
        case LOGIN_ERROR:
            return {
                ...state,
                loginError: action.payload.error,
                loading: action.payload.loading
            };
        case SIGNUP_ERROR:
            return {
                ...state,
                signupError: action.payload.error,
                loading: action.payload.loading
            };
        case SIGNUP_LOADING:
            return {
                ...state,
                loading:action.payload.loading
            }
        case REMOVE_USER:
            return {
                ...state,
                isAuthenticated: action.payload.isAuthenticated,
                user:action.payload.user,
                loading: action.payload.loading
            };
        default:
            return state;
    }
}