import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter} from 'react-router-dom';
import {Link} from "react-router-dom";
import { getInquiries, updateInquiryById } from '../../actions/inquiry-action'
import { handleAuthentication } from '../../actions/auth-action'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";

import {
    Table,
    Divider,
    Button,
    Space,
    Breadcrumb
} from 'antd';
import Footer from "../../components/Footer/Footer";


class Inquiries extends Component {

    constructor(props){
        super(props);
        this.state={
            columns:[
                {
                    title: 'Product',
                    dataIndex: 'productName',
                    key: 'productName',
                    render: (text, record) => (
                        <Link to={"/product/" + record.productId}>{record.productName}</Link>
                    ),
                },
                {
                    title: 'Quantity',
                    dataIndex: 'quantity',
                    key: 'quantity',
                },
                {
                    title: 'Contact',
                    dataIndex: 'contact',
                    key: 'contact',
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Button type="danger" shape="round" size={"small"} onClick={()=>this.updateInquiry(record, "CLOSED")}>
                                Close
                            </Button>
                        </Space>
                    ),
                }
            ],
            closedInquiriesColumns:[
                {
                    title: 'Product',
                    dataIndex: 'productName',
                    key: 'productName',
                    render: (text, record) => (
                        <Link to={"/product/" + record.productId}>{record.productName}</Link>
                    ),
                },
                {
                    title: 'Quantity',
                    dataIndex: 'quantity',
                    key: 'quantity',
                },
                {
                    title: 'Contact',
                    dataIndex: 'contact',
                    key: 'contact',
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Button type="danger" shape="round" size={"small"} onClick={()=>this.updateInquiry(record, "PENDING")}>
                                Mark Pending
                            </Button>
                        </Space>
                    ),
                }
            ]
        }
    }

    updateInquiry(inquiry, newStatus){
        inquiry.status = newStatus
        this.props.updateInquiryById(inquiry._id, inquiry);
    }

    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
        this.props.getInquiries()
    }

    getPendingInquiries(inquiries){
        let result = [];
        if(inquiries){
            for(let i=0; i<inquiries.length; i++){
                if(inquiries[i].status==="PENDING")
                    result.push(inquiries[i])
            }
        }
        return result
    }

    getClosedInquiries(inquiries){
        let result = [];
        if(inquiries){
            for(let i=0; i<inquiries.length; i++){
                if(inquiries[i].status!=="PENDING")
                    result.push(inquiries[i])
            }
        }
        return result
    }

    render() {
        let { auth,inquiries  } = this.props;
        return (

            <div>
                <Navbar auth={auth}/>
                {inquiries.loading && <Loader/>}

                <div className={"container"}>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>Inquiries</Breadcrumb.Item>
                    </Breadcrumb>
                    <Divider />
                    <div className={"row"}>
                        <div className={"col-md-12"}>
                            <div>
                                <div className={"tableTitle"} style={{display:'inline-block'}}>
                                    &nbsp;New Inquiries
                                </div>
                            </div>
                            <Table columns={this.state.columns} dataSource={this.getPendingInquiries(inquiries.inquiries)} />
                            <div className={"tableTitle"} style={{display:'inline-block'}}>
                                &nbsp;Closed Inquiries
                            </div>
                            <Table columns={this.state.closedInquiriesColumns} dataSource={this.getClosedInquiries(inquiries.inquiries)} />
                        </div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

Inquiries.propTypes = {
    getSellers:PropTypes.func.isRequired,
    handleAuthentication:PropTypes.func.isRequired,
    updateInquiryById:PropTypes.func.isRequired,
    auth: PropTypes.object,
    inquiries:PropTypes.object
}

const mapStateToProps = state  => ({
    auth:state.auth,
    inquiries:state.inquiries
})

export default connect(mapStateToProps, { getInquiries, handleAuthentication, updateInquiryById})(withRouter(Inquiries))

