import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {Redirect, withRouter} from 'react-router-dom';
import {Link} from "react-router-dom";
import { getAllProducts, updateProductById } from '../../actions/product-actions'
import { handleAuthentication } from '../../actions/auth-action'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import Footer from "../../components/Footer/Footer";
import { PlusOutlined } from '@ant-design/icons';

import {
    Table,
    Tag,
    Divider,
    Button,
    Space,
    Breadcrumb
} from 'antd';
import './InventoryManager.css';


class InventoryManager extends Component {

    constructor(props){
        super(props);
        this.state={
            columns:[
                {
                    title: 'Name',
                    dataIndex: 'name',
                    key: 'name',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Price',
                    dataIndex: 'price',
                    key: 'price',
                },
                {
                    title: 'Brand',
                    dataIndex: 'brand',
                    key: 'brand',
                },
                {
                    title: 'MOQ',
                    dataIndex: 'minimumOrderQuantity',
                    key: 'minimumOrderQuantity',
                },
                {
                    title: 'ATS',
                    dataIndex: 'availableToSellQty',
                    key: 'availableToSellQty'
                },
                {
                    title: 'Seller',
                    dataIndex: 'sellerName',
                    key: 'sellerName'
                },
                {
                    title: 'Category',
                    key: 'category',
                    dataIndex: 'category',
                },
                {
                    title: 'Sub Category',
                    key: 'subCategory',
                    dataIndex: 'subCategory',
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Button type="primary" shape="round" size={"small"}>
                                View
                            </Button>
                            <Button type="primary" shape="round" size={"small"} onClick={()=>this.approveProduct(record, "APPROVE")}>
                                Approve
                            </Button>
                            <Button type="danger" shape="round" size={"small"} onClick={()=>this.approveProduct(record, "REJECTED")}>
                                Reject
                            </Button>
                        </Space>
                    ),
                },
            ],
            approvedSellersColumns:[
                {
                    title: 'Name',
                    dataIndex: 'name',
                    key: 'name',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Price',
                    dataIndex: 'price',
                    key: 'price',
                },
                {
                    title: 'Brand',
                    dataIndex: 'brand',
                    key: 'brand',
                },
                {
                    title: 'ATS',
                    dataIndex: 'availableToSellQty',
                    key: 'availableToSellQty'
                },
                {
                    title: 'Seller',
                    dataIndex: 'sellerName',
                    key: 'sellerName'
                },
                {
                    title: 'Category',
                    key: 'category',
                    dataIndex: 'category',
                },
                {
                    title: 'Status',
                    key: 'status',
                    dataIndex: 'status',
                    render: (text, record) => (
                        <Tag color={record.isActive?'green':'geekblue'}>
                            {record.isActive?"Active":"Inactive"}
                        </Tag>
                    )
                },
                {
                    title: 'Attachments',
                    key: 'images',
                    dataIndex: 'images',
                    render: (text, record) => (
                        <div>
                            {record.images.length}
                        </div>
                    )
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Button type="primary" shape="round" size={"small"}>
                                <Link to={"viewProduct/" + record._id}>View</Link>
                            </Button>
                            <Button type={record.isActive?"danger":"primary"} shape="round" size={"small"} onClick={()=>this.updateActivation(record)}>
                                {record.isActive?"Deactivate":"Activate"}
                            </Button>

                        </Space>
                    ),
                },
            ]
        }
    }

    approveProduct(product, newStatus){
        product.status = newStatus
        this.props.updateProductById(product._id, product);
    }

    updateActivation(product){
        product.isActive = !product.isActive;
        this.props.updateProductById(product._id, product);
    }

    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
        this.props.getAllProducts()
    }

    getPendingProducts(products){
        let result = [];
        if(products){
            for(let i=0; i<products.length; i++){
                if(products[i].status==="PENDING")
                    result.push(products[i])
            }
        }
        return result
    }
    getApprovedProducts(products){
        let result = [];
        if(products){
            for(let i=0; i<products.length; i++){
                if(products[i].status!=="PENDING")
                    result.push(products[i])
            }
        }
        return result
    }

    getActiveProducts(products){
        let result = 0;
        if(products){
            for(let i=0; i<products.length; i++){
            if(products[i].isActive==true){
                result++;
            }
        }}
        return result;
    }
    getInventoryUnavailableProducts(products){
        let result = 0;
        if(products){
            for(let i=0; i<products.length; i++){
                if(products[i].availableToSellQty<=0){
                    result++;
                }
            }}
        return result;
    }
    getInactiveProducts(products){
        let result = 0;
        if(products){
            for(let i=0; i<products.length; i++){
                if(!products[i].isActive){
                    result++;
                }
            }}
        return result;
    }
    getPendingApprovalProducts(products){
        let result = 0;
        if(products){
            for(let i=0; i<products.length; i++){
                if(products[i].status!="APPROVED"){
                    result++;
                }
            }}
        return result;
    }

    render() {
        let { auth,products  } = this.props;
        return (

            <div>
                <Navbar auth={auth}/>
                {products.loading && <Loader/>}

                <div className={"container"}>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>Inventory Manager</Breadcrumb.Item>
                    </Breadcrumb>
                    <Divider />
                    <div className={"row"}>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getActiveProducts(products.products)}</div>
                                <div className={"statDetail"}>Active Products</div>
                            </div>
                        </div>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getInventoryUnavailableProducts(products.products)}</div>
                                <div className={"statDetail"}>Prducts with no inventory</div>
                            </div>
                        </div>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getInactiveProducts(products.products)}</div>
                                <div className={"statDetail"}>Inactive Products</div>
                            </div>
                        </div>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getPendingApprovalProducts(products.products)}</div>
                                <div className={"statDetail"}>Products Pending Approval</div>
                            </div>
                        </div>
                    </div>
                    <div className={"row"}>
                        <div className={"col-md-12"}>
                            <div>
                                <div className={"tableTitle"} style={{display:'inline-block'}}>
                                    &nbsp;Products Pending Approval
                                </div>
                                <Link to={"addProduct"}><Button type="primary" icon={<PlusOutlined />} shape="round" size={"large"} style={{display:'inline-block', marginLeft:'1rem'}}>
                                    Add Product
                                </Button></Link>
                            </div>
                            <Table columns={this.state.columns} dataSource={this.getPendingProducts(products.products)} />
                            <div className={"tableTitle"} style={{display:'inline-block'}}>
                                &nbsp;Approved Products
                            </div>
                            <Table columns={this.state.approvedSellersColumns} dataSource={this.getApprovedProducts(products.products)} tableLayout={"auto"} />
                        </div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

InventoryManager.propTypes = {
    getAllProducts:PropTypes.func.isRequired,
    handleAuthentication:PropTypes.func.isRequired,
    updateProductById:PropTypes.func.isRequired,
    auth: PropTypes.object,
    products:PropTypes.object
}

const mapStateToProps = state  => ({
    auth:state.auth,
    products:state.products
})

export default connect(mapStateToProps, { getAllProducts, updateProductById, handleAuthentication})(withRouter(InventoryManager))

