import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {withRouter } from 'react-router-dom';

import {Link} from "react-router-dom";
import { handleAuthentication } from '../../actions/auth-action'
import { getCustomers } from '../../actions/customers-action'
import { getProductById } from '../../actions/product-actions'
import { addNewInquiry } from '../../actions/inquiry-action'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import certified from '../../assets/certified.png'
import {
    Button,
    Carousel,
    Breadcrumb,
    Divider,
    Descriptions,
    Modal,
    Form,
    Input,
    InputNumber,
    notification
} from 'antd';
import './ProductPage.css';
import Footer from "../../components/Footer/Footer";

class ProductPage extends Component {

    constructor(props){
        super(props);
        this.state={
            subCategories:[],
            images:[],
            visible:false
        }
    }

    onFinish = values => {
        console.log('Received values of form: ', values);
        let payload = {
            email:"",
            quantity:values.quantity,
            contact:values.contact,
            productId:this.props.products.selectedProduct._id,
            productName:this.props.products.selectedProduct.name,
            status:"PENDING"
        }
        console.log(payload)
        this.props.addNewInquiry(payload, this.openNotification())
    };

    openNotification = () => {
        this.setState({visible:false})
        notification.open({
            message: 'Inquiry Submission',
            description:
                'Your inquiry has been submitted. Our customer representative will get back to you soon!',
            onClick: () => {
                console.log('Notification Clicked!');
            },
        });
    };


    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
        this.props.getProductById(this.props.match.params.id)
    }

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };


    onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };


    render() {
        let { auth, products, inquiries } = this.props;
        return (
            <div>
                <Navbar auth={auth}/>
                {(products.loading || inquiries.loading) && <Loader/>}
                {/*{!auth.isAuthenticated?<Redirect to={"/"} />:*/}
                <div style={{minHeight:'70vh'}}>
                {products.selectedProduct && <div className={"container"}>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            <Link to={"/category/" + products.selectedProduct.category + "/" +products.selectedProduct.subCategory}>{products.selectedProduct.category}</Link>
                        </Breadcrumb.Item>
                        {/*<Breadcrumb.Item>*/}
                        {/*    <Link >{products.selectedProduct.subCategory}</Link>*/}
                        {/*</Breadcrumb.Item>*/}
                        <Breadcrumb.Item>{products.selectedProduct.name}</Breadcrumb.Item>
                    </Breadcrumb>
                    <Divider/>
                    <div className={"productTab"}>
                        <div className={"row"} >

                            <div className={"col-md-4"}>
                                <Carousel autoplay>
                                    {
                                        products.selectedProduct.images.map((item, index) => (
                                            <div className={"productTabMovingImage"}>
                                                <img src={item} style={{width:"100%"}}/>
                                            </div>
                                        ))
                                    }
                                </Carousel>
                            </div>
                            <div className={"col-md-8"}>
                                <div className={"productTabName"}>
                                    {products.selectedProduct.name}
                                </div>
                                <div className={"productPageSellerName"}>
                                    Sold by {products.selectedProduct.sellerName.split(" ")[0]}
                                </div>
                                <div>
                                    <div className={"productTabPrice"}>
                                         {products.selectedProduct.price}
                                    </div>
                                </div>
                                <div className={"deliveryInstruction"}><b>FREE DELIVERY</b> on orders above {products.selectedProduct.minimumOrderQuantity} units.</div>
                                <div className={"certified"}>
                                    <img src={certified}/>
                                </div>
                                <div>
                                    <Button
                                        type="primary"
                                        onClick={() => {
                                            this.setState({visible:true})
                                        }}
                                        size={"large"}
                                        shape={"round"}
                                        style={{marginTop:"2rem"}}
                                    >
                                        Get Quotation
                                    </Button>
                                    <Modal
                                        title="Tell us what you need and we'll get right back at you!"
                                        visible={this.state.visible}
                                        centered
                                        onCancel={this.handleCancel}
                                        footer={null}
                                    >
                                        <Form
                                            labelCol={{ span: 24 }}
                                            wrapperCol={{ span: 24 }}
                                            name="basic"
                                            initialValues={{ remember: true }}
                                            onFinish={this.onFinish}
                                            onFinishFailed={this.onFinishFailed}
                                        >
                                            <Form.Item
                                                label="Quantity"
                                                name="quantity"
                                                rules={[{ required: true, message: 'Please enter required quantity!' }]}
                                            >
                                                <InputNumber style={{width:'100%'}}/>
                                            </Form.Item>
                                            <Form.Item
                                                label="Contact Number"
                                                name="contact"
                                                rules={[{ required: true, message: 'Please tell us your contact number!' }]}
                                            >
                                                <Input />
                                            </Form.Item>
                                            <Form.Item
                                                        wrapperCol={{ span: 24 }}>
                                                <Button type="primary" htmlType="submit" >
                                                    Submit
                                                </Button>
                                            </Form.Item>
                                        </Form>
                                    </Modal>
                                </div>
                            </div>
                        </div>
                        <div className={"descriptionSection"}>
                            <div className={"productTabDescription"}>
                                {products.selectedProduct.description}
                            </div>
                        </div>
                        <Descriptions
                            title="Product Details"
                            bordered
                            column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
                            style={{padding:'1rem'}}
                        >
                            <Descriptions.Item label="Brand">{products.selectedProduct.brand}</Descriptions.Item>
                            <Descriptions.Item label="Material">{products.selectedProduct.material}</Descriptions.Item>
                            <Descriptions.Item label="Size">{products.selectedProduct.size}</Descriptions.Item>
                            <Descriptions.Item label="Usage">{products.selectedProduct.usage}</Descriptions.Item>
                            <Descriptions.Item label="Packaging Details">{products.selectedProduct.packagingDetails}</Descriptions.Item>
                            <Descriptions.Item label="Minimum Order Quantity">{products.selectedProduct.minimumOrderQuantity}</Descriptions.Item>
                            <Descriptions.Item label="Color">{products.selectedProduct.color}</Descriptions.Item>
                        </Descriptions>
                        <div className={"productImages"}>
                            <div className={"productImagesTitle"}>Product Images</div>
                            {
                                products.selectedProduct.images.map((item, index) => (
                                    <div className={"productImageFull"}>
                                        <img src={item}/>
                                    </div>
                                ))
                            }
                        </div>
                    </div>

                </div>
                }
                </div>
                <Footer/>
            </div>
        );
    }
}

ProductPage.propTypes = {
    handleAuthentication:PropTypes.func.isRequired,
    addNewCustomer:PropTypes.func.isRequired,
    getProductById:PropTypes.func.isRequired,
    addNewInquiry:PropTypes.func.isRequired,
    auth: PropTypes.object,
    customers:PropTypes.object,
    products:PropTypes.object,
    inquiries:PropTypes.object
}

const mapStateToProps = state  => ({
    auth:state.auth,
    customers:state.customers,
    products:state.products,
    inquiries:state.inquiries
})

export default connect(mapStateToProps, {handleAuthentication, getCustomers, getProductById, addNewInquiry})(withRouter(ProductPage))

