import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {withRouter } from 'react-router-dom';

import {Link} from "react-router-dom";
import { handleAuthentication } from '../../actions/auth-action'
import { getCustomers } from '../../actions/customers-action'
import { getAllProductsByCategory } from '../../actions/product-actions'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import Footer from "../../components/Footer/Footer";
import { Empty } from 'antd';
import {
    Breadcrumb,
    Divider
} from 'antd';
import './Category.css';
import { CATEGORIES } from '../../actions/constants'
import ProductTab from "../../components/ProductTab/ProductTab";

class Category extends Component {

    constructor(props){
        super(props);
        this.state={
            subCategories:[],
            images:[],
            selectedSubCategory:this.props.match.params.subCategory
        }
    }

    getActiveProducts(products){
        let result = [];
        if(products){
            for(let i=0; i<products.length; i++){
                if(products[i].isActive===true){
                    result.push(products[i])
                }
            }
        }
        return result
    }


    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }

        this.props.getAllProductsByCategory(this.props.match.params.category)
        for(let i=0; i<CATEGORIES.length; i++){
            if(CATEGORIES[i].name===this.props.match.params.category){
                this.setState({subCategories:CATEGORIES[i].subCategories})
            }
        }
    }

    updateSubcategory(subCategory){
        this.setState({selectedSubCategory:subCategory})
    }


    render() {
        let { auth, products } = this.props;

        return (
            <div>
                <Navbar auth={auth}/>
                {products.loading && <Loader/>}
                <div style={{minHeight:'70vh'}}>
                {products.categoryProducts && <div className={"container"}>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            <Link >{this.props.match.params.category}</Link>
                        </Breadcrumb.Item>
                    </Breadcrumb>
                    <Divider/>
                    <div className={"subCategoryList"}>
                        <div onClick={()=>this.updateSubcategory("all")} className={"subCategoryItem"} style={{backgroundColor:this.state.selectedSubCategory=="all"?'#48a4e3':'transparent',color:this.state.selectedSubCategory=="all"?'white':'#48a4e3'}}>All</div>
                        {
                            this.state.subCategories
                                .map((item, index) => (
                                <div key={index} onClick={()=>this.updateSubcategory(item)} className={"subCategoryItem"} style={{backgroundColor:this.state.selectedSubCategory==item?'#48a4e3':'transparent',color:this.state.selectedSubCategory==item?'white':'#48a4e3'}}>{item}</div>
                            ))
                        }
                    </div>
                    <div className={"allProducts"}>
                        {
                            this.getActiveProducts(products.categoryProducts)
                                .filter(item => this.state.selectedSubCategory!=="all"?item.subCategory===this.state.selectedSubCategory: true)
                                .map((product, index) => (
                                <ProductTab product={product}/>
                            ))
                        }
                        {
                            this.getActiveProducts(products.categoryProducts).filter(item => (item.subCategory===this.state.selectedSubCategory || this.state.selectedSubCategory==='all')).length<1 &&
                            <div className={"emptyProducts"}>
                                <Empty/>
                            </div>
                        }
                    </div>

                </div>
                }
                </div>
                <Footer/>
            </div>
        );
    }
}

Category.propTypes = {
    handleAuthentication:PropTypes.func.isRequired,
    addNewCustomer:PropTypes.func.isRequired,
    getAllProductsByCategory:PropTypes.func.isRequired,
    auth: PropTypes.object,
    customers:PropTypes.object,
    products:PropTypes.object
}

const mapStateToProps = state  => ({
    auth:state.auth,
    customers:state.customers,
    products:state.products
})

export default connect(mapStateToProps, {handleAuthentication, getCustomers, getAllProductsByCategory})(withRouter(Category))

