import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import {Link, Redirect} from "react-router-dom";
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import { login, handleAuthentication } from '../../actions/auth-action'

import {
    Form,
    Input,
    Button,
    Alert
} from 'antd';
import './Login.css';
import Footer from "../../components/Footer/Footer";

class Login extends Component {

    constructor(props){
        super(props);
        this.state={
            username:"",
            password:""
        }
    }

    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
    }

    onFinish = values => {
        console.log('Received values of form: ', values);
        let payload = {
            username:this.state.email,
            password:this.state.password
        }
        console.log(payload)
        this.props.login(payload)
    };
    render() {
        let { auth  } = this.props;
        return (
            <div>
                {auth.isAuthenticated?<Redirect to={"/"} />:
                    <div>
                        <Navbar auth={auth}/>

                        {auth.loading && <Loader/>}
                        <div className="example">

                        </div>
                        <div className={"row"}>
                            <div className={"col-md-4"}>

                            </div>
                            <div className={"col-md-4"}>
                                <div className={"loginForm"}>
                                    <div className={"loginFormHeader"}>
                                        <div className={"loginHeaderText"}>Welcome back!</div>
                                        <div className={"loginHeaderText2"}>Please provide your email and password to proceed.</div>
                                    </div>
                                    <Form name="register" onFinish={this.onFinish} scrollToFirstError labelCol={{ span: 24 }} wrapperCol={{ span: 24 }}>
                                        <Form.Item name="email" label="E-mail" rules={[{type: 'email', message: 'The input is not valid E-mail!'}, {required: true, message: 'Please input your E-mail!'}]} style={{marginTop:"-0.5rem"}}>
                                            <Input  onChange={(text)=>this.setState({email:text.target.value})} style={{marginTop:"-1.5rem"}}/>
                                        </Form.Item>
                                        <Form.Item name="password" label="Password" rules={[{required: true, message: 'Please input your password!'}]} hasFeedback style={{marginTop:"-0.5rem"}}>
                                            <Input.Password  onChange={(text)=>this.setState({password:text.target.value})} style={{marginTop:"-1.5rem"}}/>
                                        </Form.Item>
                                        <p><i>New here? <Link to={"/signup"}>Signup here</Link></i></p>
                                        <Form.Item >
                                            <Button htmlType="submit" style={{backgroundColor:"rgb(224, 224, 224)", color:"#3c3c3c", borderRadius:"20px"}}>
                                                Signin
                                            </Button>
                                        </Form.Item>
                                        { auth.loginError &&
                                            <Alert
                                                message="Error"
                                                description={auth.loginError}
                                                type="error"
                                                showIcon
                                                closable={true}
                                            />
                                        }
                                    </Form>
                                </div>
                            </div>
                        </div>
                    </div>}
                    <Footer/>
            </div>

        );
    }
}

Login.propTypes = {
    login:PropTypes.func,
    auth: PropTypes.object,
}

const mapStateToProps = state  => ({
    auth:state.auth,
})

export default connect(mapStateToProps, {login,handleAuthentication})(withRouter(Login))

