import axios from 'axios';
import {
    BASE_URL,
    GET_ALL_PRODUCTS,
    GET_SELLER_PRODUCTS,
    GET_CATEGORY_PRODUCT,
    PRODUCTS_LOADING,
    GET_PRODUCT_BY_ID,
    UPDATE_PRODUCT_BY_ID,
    DELETE_PRODUCT,
    PRODUCTS_ERROR,
    GET_TRENDING_PRODUCTS
} from './constants'

export const getAllProducts = () => dispatch => {
    dispatch({
        type: PRODUCTS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "product/products")
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_ALL_PRODUCTS,
                    payload: {
                        products:response.data.products,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: PRODUCTS_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const addProduct = (body) => dispatch => {
    dispatch({
        type: PRODUCTS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.post(BASE_URL + "product/products", body)
        .then(res => {
            if(res.data.status==="success"){
                axios.get(BASE_URL + "product/products")
                    .then(response => {
                        if(response.data.status==="success"){
                            dispatch({
                                type: GET_ALL_PRODUCTS,
                                payload: {
                                    products:response.data.products,
                                    loading: false
                                }
                            });
                            alert("Product added successfully.")
                        }
                    })
                    .catch(error => {
                        dispatch({
                            type: PRODUCTS_ERROR,
                            payload: {
                                loading:false
                            }
                        });
                    })
            }

        })
        .catch(error => {
            dispatch({
                type: PRODUCTS_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

