export const updateProductById = (id, body) => dispatch => {
    dispatch({
        type: PRODUCTS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.put(BASE_URL + "product/product/" + id, body)
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: UPDATE_PRODUCT_BY_ID,
                    payload: {
                        selectedProduct:response.data.products
                    }
                });
                axios.get(BASE_URL + "product/products")
                    .then(response => {
                        if(response.data.status==="success"){
                            dispatch({
                                type: GET_ALL_PRODUCTS,
                                payload: {
                                    products:response.data.products,
                                    loading: false
                                }
                            });
                        }
                    })
                    .catch(error => {
                        dispatch({
                            type: PRODUCTS_ERROR,
                            payload: {
                                loading:false
                            }
                        });
                    })
            }

        })
        .catch(error => {
            dispatch({
                type: PRODUCTS_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}
export const deleteProductById = (id) => dispatch => {
    dispatch({
        type: PRODUCTS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.put(BASE_URL + "product/product/" + id)
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: DELETE_PRODUCT,
                    payload: {
                        deleteStatus:response.data,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: PRODUCTS_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

