export const updateSelectedProduct = (selectedProduct, key, value) => dispatch => {
    selectedProduct[key] = value;
    dispatch({
        type: GET_PRODUCT_BY_ID,
        payload: {
            selectedProduct:selectedProduct,
            loading: false
        }
    });
}