import React from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";

import { Provider } from 'react-redux';
import store from './store'

import Home from './containers/App/App';
import Logout from "./containers/Logout";
import Login from './containers/Login/Login';
import Signup from './containers/Signup/Signup';
import SellerManager from './containers/SellerManager/SellerManager';
import AddSeller from './containers/SellerManager/AddSeller'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
import {PrivateRoute} from "./HOC/private-route";
import InventoryManager from "./containers/InventoryManager/InventoryManager";
import AddProduct from "./containers/InventoryManager/AddProduct";
import ViewProduct from "./containers/InventoryManager/ViewProduct";
import ViewSeller from "./containers/SellerManager/ViewSeller";
import ProductPage from "./containers/Products/ProductPage";
import Category from "./containers/Category/Category";
import ReferSeller from "./containers/ReferSeller/ReferSeller";
import Inquiries from "./containers/Inquiries/Inquiries"
import ScrollToTop from "./ScrollToTop";
import { PhoneOutlined, MailOutlined } from '@ant-design/icons';

function App() {
  return (
      <Provider store = { store }>
          <Router>
              <ScrollToTop/>
              <Route exact path="/" component={Home} />
              <Route exact path="/home" component={Home} />
              <Route exact path="/logout" component={Logout} />
              <Route exact path="/login" component={Login} />
              <Route exact path="/signup" component={Signup} />
              <Route exact path="/sellerManager" component={SellerManager} />
              <Route exact path="/addSeller" component={AddSeller} />
              <Route exact path="/inventoryManager" component={InventoryManager} />
              <Route exact path="/addProduct" component={AddProduct} />
              <Route exact path="/viewProduct/:id" component={ViewProduct} />
              <Route exact path="/product/:id" component={ProductPage} />
              <Route exact path="/category/:category/:subCategory" component={Category} />
              <Route exact path="/viewSeller/:id" component={ViewSeller} />
              <Route exact path="/referSeller" component={ReferSeller}/>
              <Route exact path="/inquiries" component={Inquiries}/>
              <PrivateRoute exact path="/docs" component={Home}/>
          </Router>
          <div className={"fixedTabs"}>
              <a href={"tel:18005995559"}><PhoneOutlined /></a><br/>
              <a href={"mailto:info@simplekart.in"}><MailOutlined /></a>
          </div>
      </Provider>
  );
}

export default App;
