import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {Link, Redirect, withRouter} from 'react-router-dom';
import { signup, handleAuthentication } from '../../actions/auth-action'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import {
    Form,
    Input,
    Button,
    Alert
} from 'antd';
import './Signup.css';
import Footer from "../../components/Footer/Footer";

//const [form] = Form.useForm();

class Signup extends Component {

    constructor(props){
        super(props);
        this.state={
            gstFileName:""
        }
    }

    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
    }
    onFinish = values => {
        console.log('Received values of form: ', values);
        let payload = {
            name:this.state.name,
            email:this.state.email,
            username:this.state.email,
            password:this.state.password,
            contact:this.state.phone,
            isActive:true,
            status:"PENDING",
            tinNumber:"",
            gstFileUrl: "",
            gstFileStatus:"",
            eligibleToSell:false,
            role:"CUSTOMER"
        }
        this.props.signup(payload)
    };

    render() {
        let { auth  } = this.props;

        return (
            <div>
                <Navbar auth={auth}/>
                {auth.loading && <Loader/>}
                {auth.isAuthenticated?<Redirect to={"/"} />:
                    <div className={"row"}>
                        <div className={"col-md-4"}>

                        </div>
                        <div className={"col-md-4"}>
                            <div className={"signupForm"}>
                                <div className={"loginFormHeader"}>
                                    <div className={"loginHeaderText"}>Welcome to SimpleKart!</div>
                                    <div className={"loginHeaderText2"}>Please provide below information to proceed .</div>
                                </div>
                                <Form name="register" onFinish={this.onFinish} scrollToFirstError labelCol={{ span: 24 }} wrapperCol={{ span: 24 }}>

                                    <Form.Item name="name" label="Name" rules={[{required: true, message: 'Please input your E-mail!'}]}  style={{marginTop:"-0.5rem"}}>
                                        <Input  onChange={(text)=>this.setState({name:text.target.value})} style={{marginTop:"-1.5rem"}}/>
                                    </Form.Item>
                                    <Form.Item name="email" label="E-mail" rules={[{type: 'email', message: 'The input is not valid E-mail!'}, {required: true, message: 'Please input your E-mail!'}]} style={{marginTop:"-0.5rem"}}>
                                        <Input  onChange={(text)=>this.setState({email:text.target.value})} style={{marginTop:"-1.5rem"}}/>
                                    </Form.Item>

                                    <Form.Item name="password" label="Password" rules={[{required: true, message: 'Please input your password!'}]} hasFeedback style={{marginTop:"-0.5rem"}}>
                                        <Input.Password  onChange={(text)=>this.setState({password:text.target.value})} style={{marginTop:"-1.5rem"}}/>
                                    </Form.Item>

                                    <Form.Item name="confirm" label="Confirm Password" dependencies={['password']} hasFeedback
                                        rules={[{required: true, message: 'Please confirm your password!'},
                                            ({ getFieldValue }) => ({
                                                validator(rule, value) {
                                                    if (!value || getFieldValue('password') === value) {
                                                        return Promise.resolve();
                                                    }
                                                    return Promise.reject('The two passwords that you entered do not match!');
                                                }
                                            })
                                        ]} style={{marginTop:"-0.5rem"}}
                                    >
                                        <Input.Password  onChange={(text)=>this.setState({confirmPassword:text.target.value})} style={{marginTop:"-1.5rem"}}/>
                                    </Form.Item>
                                    <Form.Item name="phone" label="Phone Number" rules={[{ required: true, message: 'Please input your phone number!' }]} style={{marginTop:"-0.5rem"}}>
                                        <Input addonBefore={"+91"} style={{ width: '100%' }}  onChange={(text)=>this.setState({phone:text.target.value})} style={{marginTop:"-0.5rem"}}/>
                                    </Form.Item>
                                    <p><i>Already have an account? <Link to={"/login"}>Login here</Link></i></p>
                                    <Form.Item >
                                        <Button htmlType="submit" style={{backgroundColor:"rgb(224, 224, 224)", color:"#3c3c3c", borderRadius:"20px"}}>
                                            Register
                                        </Button>
                                    </Form.Item>
                                    { auth.signupError &&
                                    <Alert
                                        message="Error"
                                        description={auth.signupError}
                                        type="error"
                                        showIcon
                                        closable={true}
                                    />
                                    }
                                </Form>
                            </div>
                        </div>
                    </div>
                }
                <Footer/>

            </div>
        );
    }
}

Signup.propTypes = {
    signup:PropTypes.func.isRequired,
    handleAuthentication:PropTypes.func.isRequired,
    auth: PropTypes.object,
}

const mapStateToProps = state  => ({
    auth:state.auth,
})

export default connect(mapStateToProps, {signup,handleAuthentication})(withRouter(Signup))

