import React, { Component } from 'react';
import './ProductTab.css'
import { Carousel,Space } from 'antd';
import {Link} from "react-router-dom";

class ProductTab extends Component {
    constructor(props){
        super(props);
        this.state = {

        }
    }
    render() {
        return (
            <div className={"productTab"} style={{boxShadow: "0px 3px 5px #e8e8e8"}}>
                <div className={"row"}>

                    <div className={"col-md-3"}>
                        <Carousel>
                        {
                            this.props.product.images.map((item, index) => (
                                <Link to={"/product/" + this.props.product._id}>
                                    <div className={"categoryProductImage"}>
                                        <img src={item} style={{width:"100%"}}/>
                                    </div>
                                </Link>
                            ))
                        }
                        </Carousel>
                    </div>
                    <div className={"col-md-9"}>
                        <div className={"productTabName"} style={{fontSize:'1.4rem'}}>
                            {this.props.product.name}
                        </div>
                        <div>
                            <div className={"productTabPrice"}>
                                 {this.props.product.price}
                            </div>
                        </div>
                        <div>
                            <div className={"productTabDescription"} style={{fontSize:"0.9rem"}}>
                                {this.props.product.description.substr(0,240) + " ..."}
                            </div>
                        </div>
                        <div>
                            <div className={"productTabDetailsLabel"}>
                                Material
                            </div>
                            <div className={"productTabDetailsValue"}>
                                {this.props.product.material}
                            </div>
                        </div>
                        <div>
                            <div className={"productTabDetailsLabel"}>
                                Usage
                            </div>
                            <div className={"productTabDetailsValue"}>
                                {this.props.product.usage}
                            </div>
                        </div>
                        <div>
                            <div className={"productTabDetailsLabel"}>
                                Packaging Details
                            </div>
                            <div className={"productTabDetailsValue"}>
                                {this.props.product.packagingDetails}
                            </div>
                        </div>
                        <div>
                            <div className={"productTabDetailsLabel"}>
                                Size
                            </div>
                            <div className={"productTabDetailsValue"}>
                                {this.props.product.size}
                            </div>
                        </div>
                        <div className={"viewMore"}>
                            <Link to={"/product/" + this.props.product._id}>View More</Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ProductTab;