import {
    BASE_URL,
    GET_INQUIRIES,
    GET_INQUIRY_BY_ID,
    INQUIRIES_ERROR,
    INQUIRIES_LOADING
} from '../actions/constants'
const initialState = {
};

export default function(state = initialState, action) {
    switch(action.type) {
        case INQUIRIES_LOADING:
            return {
                ...state,
                loading: action.payload.loading
            }
        case GET_INQUIRIES:
            return {
                ...state,
                loading: action.payload.loading,
                inquiries: action.payload.inquiries
            };
        case INQUIRIES_ERROR:
            return {
                ...state,
                error: action.payload.error,
                loading: action.payload.loading
            };
        case GET_INQUIRY_BY_ID:
            return {
                ...state,
                selectedInquiry:action.payload.selectedInquiry,
                loading:action.payload.loading
            }
        default:
            return state;
    }
}
