import axios from 'axios';
import {
    BASE_URL,
    GET_SELLERS,
    GET_SELLER_BY_ID,
    UPDATE_SELLER,
    GET_USER_SELLERS,
    SELLERS_LOADING,
    SELLER_ERROR,
    GET_PRODUCT_BY_ID
} from './constants'

export const getSellers = () => dispatch => {
    dispatch({
        type: SELLERS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "sellers/sellers")
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_SELLERS,
                    payload: {
                        sellers:response.data.sellers,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: SELLER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const getSellerById = (id) => dispatch => {
    dispatch({
        type: SELLERS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "sellers/sellers/" + id)
        .then(response => {
            if(response.data.status==="success"){
                dispatch({
                    type: GET_SELLER_BY_ID,
                    payload: {
                        seller:response.data.seller,
                        loading: false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: SELLER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

export const updateSellerById = (id, body) => dispatch => {
    dispatch({
        type: SELLERS_LOADING,
        payload: {
            loading:true
        }
    });
    axios.put(BASE_URL + "sellers/sellers/" + id, body)
        .then(response => {
            if(response.data.status==="success"){
                axios.get(BASE_URL + "sellers/sellers")
                    .then(res => {
                        if(res.data.status==="success"){
                            dispatch({
                                type: GET_SELLERS,
                                payload: {
                                    sellers:res.data.sellers,
                                    loading: false
                                }
                            });
                        }

                    })
                    .catch(error => {
                        dispatch({
                            type: SELLER_ERROR,
                            payload: {
                                loading:false
                            }
                        });
                    })
            }

        })
        .catch(error => {
            dispatch({
                type: SELLER_ERROR,
                payload: {
                    loading:false
                }
            });
        })
}

