export const updateSelectedSeller = (selectedSeller, key, value) => dispatch => {
    selectedSeller[key] = value;
    dispatch({
        type: GET_SELLER_BY_ID,
        payload: {
            seller:selectedSeller,
            loading: false
        }
    });
}
