import axios from 'axios';
import { REMOVE_USER, SET_CURRENT_USER, BASE_URL, SIGNUP_LOADING, SIGNUP_ERROR, LOGIN_ERROR} from './constants'

export const handleAuthentication = () => dispatch => {
    if(localStorage.getItem("cvd_id")){
        let user = {
            userId: localStorage.getItem('cvd_id'),
            status: localStorage.getItem('cvd_status'),
            isActive: localStorage.getItem('cvd_isActive'),
            gstFileUrl: localStorage.getItem('cvd_gstFileUrl'),
            tinNumber: localStorage.getItem('cvd_tinNumber'),
            contact: localStorage.getItem('cvd_contact'),
            name: localStorage.getItem('cvd_name'),
            eligibleToSell: localStorage.getItem('cvd_eligibleToSell'),
            gstFileStatus: localStorage.getItem('cvd_gstFileStatus'),
            role:localStorage.getItem('cvd_role')
        }
        dispatch({
            type: SET_CURRENT_USER,
            payload: {
                user:user,
                isAuthenticated:true,
                loading: false
            }
        });
    }
    else{
        dispatch({
            type: SET_CURRENT_USER,
            payload: {
                user:null,
                isAuthenticated:false,
                loading: false
            }
        });
    }


}

export const signup = (payload) => dispatch => {
    dispatch({
        type: SIGNUP_LOADING,
        payload: {
            loading:true
        }
    });
    axios.post(BASE_URL + "auth/signup", payload)
        .then(response => {
            if(response.data.status==="success"){
                localStorage.setItem('cvd_id', response.data.user._id);
                localStorage.setItem('cvd_status', response.data.user.status);
                localStorage.setItem('cvd_isActive', response.data.user.isActive);
                localStorage.setItem('cvd_gstFileUrl', response.data.user.gstFileUrl);
                localStorage.setItem('cvd_tinNumber', response.data.user.tinNumber);
                localStorage.setItem('cvd_contact', response.data.user.contact);
                localStorage.setItem('cvd_name', response.data.user.name);
                localStorage.setItem('cvd_eligibleToSell', response.data.user.eligibleToSell)
                localStorage.setItem('cvd_gstFileStatus', response.data.user.gstFileStatus)
                localStorage.setItem('cvd_role', response.data.user.role)
                dispatch({
                    type: SET_CURRENT_USER,
                    payload: {
                        user:response.data.user,
                        isAuthenticated:true,
                        loading: false
                    }
                });
                window.history.pushState({}, document.title, "/");
            }
            else if(response.data.status==="failure"){
                dispatch({
                    type: SIGNUP_ERROR,
                    payload: {
                        error: response.data.error,
                        isAuthenticated:true,
                        loading:false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: SIGNUP_ERROR,
                payload: {
                    error: error,
                    isAuthenticated:true,
                    loading:false
                }
            });
        })
}

