export const logout = () => dispatch => {
    dispatch({
        type: SIGNUP_LOADING,
        payload: {
            loading:true
        }
    });
    setTimeout(function(){
        localStorage.removeItem('cvd_id');
        localStorage.removeItem('cvd_status');
        localStorage.removeItem('cvd_isActive');
        localStorage.removeItem('cvd_gstFileUrl');
        localStorage.removeItem('cvd_tinNumber');
        localStorage.removeItem('cvd_contact');
        localStorage.removeItem('cvd_name');
        localStorage.removeItem('cvd_eligibleToSell')
        localStorage.removeItem('cvd_gstFileStatus')
        localStorage.removeItem('cvd_role')
        dispatch({
            type: REMOVE_USER,
            payload: {
                isAuthenticated:false,
                user:null,
                loading:false
            }
        });
    }, 3000);

}
