import {
    GET_ALL_PRODUCTS,
    GET_SELLER_PRODUCTS,
    GET_CATEGORY_PRODUCT,
    PRODUCTS_LOADING,
    GET_PRODUCT_BY_ID,
    UPDATE_PRODUCT_BY_ID,
    DELETE_PRODUCT,
    PRODUCTS_ERROR,
    GET_TRENDING_PRODUCTS
} from '../actions/constants';
const initialState = {
};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_ALL_PRODUCTS:
            return {
                ...state,
                loading: action.payload.loading,
                products: action.payload.products

            }
        case GET_SELLER_PRODUCTS:
            return  {
                ...state,
                loading: action.payload.loading,
                sellerProducts: action.payload.sellerProducts
            }
        case GET_CATEGORY_PRODUCT:
            return  {
                ...state,
                loading: action.payload.loading,
                categoryProducts: action.payload.categoryProducts
            }
        case PRODUCTS_LOADING:
            return {
                ...state,
                loading: action.payload.loading,
                selectedProduct: null
            }
        case GET_PRODUCT_BY_ID:
            return {
                ...state,
                loading: action.payload.loading,
                selectedProduct: action.payload.selectedProduct
            }
        case UPDATE_PRODUCT_BY_ID:
            return {
                ...state,
                loading: action.payload.loading,
                selectedProduct: action.payload.selectedProduct
            }
        case DELETE_PRODUCT:
            return {
                ...state,
                loading: action.payload.loading,
                deleteStatus: action.payload.deleteStatus
            }
        case GET_TRENDING_PRODUCTS:
            return {
                ...state,
                loading: action.payload.loading,
                trendingProducts: action.payload.trendingProducts
            }

        default:
            return state;
    }
}
